@import '../partials/variables';
@import 'theme1.scss';

//Style Two
#atw_style2{
    @extend #atw_style1;
    .atw_item{
        &:hover{
            .atw_item_top{
                .product-color{
                    .owl-dots{
                        visibility: visible;
                        opacity: 1;
                    }
                }
            }
        }
        .atw_item_top{
            .product-color{
                .owl-dots{
                    position: absolute;
                    right: 15px;
                    top: 18px;
                    visibility: hidden;
                    opacity: 0;
                    transition: 0.3s ease;
                    .owl-dot{
                        width: 12px;
                        height: 12px;
                        background: #fff;
                        border-radius: 50%;
                        margin-bottom: 10px;
                        padding: 3px;
                        position: relative;
                        &.active{
                            position: relative;
                            &:before{
                                position: absolute;
                                content: "";
                                left: -5px;
                                top: -5px;
                                width: 22px;
                                height: 22px;
                                border: 2px solid #fff;
                                border-radius: 50%;
                            }
                        }
                        &:nth-child(2){
                            background:#383c59;
                        }
                        &:nth-child(3){
                            background:#fc8346;
                        }
                        &:last-child{
                            margin-bottom: 0;
                        }
                    }
                }
            }
            .atw_overlay_content2{
                z-index: 1;
                background: none;
                text-align: center;
            }
        }
        .atw_item_bottom{
            .atw_item_title{
                text-align: center;
            }
            .atw_item_info{
                display: block;
                text-align: center;
                .atw_price, .atw_rating{
                    display: block;
                }
            }
        }
    }
    .atw_slider_controls{
        text-align: center;
        .slider--control2{
            margin: 40px 4px 0 4px;
            position: inherit;
            top: auto;
            transform: none;
            border-radius: 0;
            transition: 0.3s ease;
            &:hover{
                background: $theme1;
                color: #fff;
                border-color: $theme1;
            }
        }
    }
}