@import '../partials/variables';
/* style one */
#atw_style1{
    .atw_item{
        .atw_overlay_content{
            width: calc( 100% - 42px);
            left: 21px;
            bottom: 0px;
            background: #fff;
            opacity: 0;
        }
        .atw_badge{
            right:10px;
            top: 10px;
            span{
                background: $theme1;
                border-radius: 500px;
                color: #fff;
                display: inline-block;
                line-height: 50px;
                width: 50px;
                font-size: 14px;
                text-align: center;
            }
        }
        .atw_product_actions{
            display: flex;
            justify-content: space-around;
            padding: 16px 0 13px 0;
            box-shadow: 0 5px 5px transparentize(#544d7c, 0.9);
            li{
                a{
                    font-size: 18px;
                    color: #363940;
                    transition: $transition;
                    &:hover{
                        color: $theme1;
                    }
                }
            }
        }
        &:hover{
            .atw_item_top{
                .atw_overlay_content{
                    opacity: 1;
                    bottom: 20px;
                }
            }
        }
        .atw_item_bottom{
            padding-top: 20px;
            .atw_item_title{
                font-weight: 400;
                a{
                    color: $titleColor;
                    font-size: 16px;
                    line-height: 26px;
                    &:hover{
                        color: $theme1;
                    }
                }
            }
            .atw_item_info{
                display: flex;
                justify-content: space-between;
                align-items: center;
                .atw_price{
                    color: $theme1;
                    line-height: 48px;
                    del{
                        color: #9192a3;
                        font-weight: 300;
                        font-size: 14px;
                    }
                    .atw_discount_ratio{
                        border: 1px solid $theme1;
                        font-size: 12px;
                        color: $theme1;
                        line-height: 20px;
                        padding: 0 7px;
                        display: inline-block;
                        border-radius: 2px;
                        margin-left: 4px;
                    }
                }
            }
        }
    }

    .slider_control{
        position: absolute;
        border: 1px solid $borderColor;
        top: 50%;
        transform: translateY(-50%);
        &.icon-arrow-left{
            left: 0;
        }
        &.icon-arrow-right{
            right: 0;
        }
    }
}
